body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(250,250,250)), to(rgb(200,200,200))) fixed;
  background: -webkit-linear-gradient(rgb(250,250,250), rgb(200,200,200)) fixed;
  background: -o-linear-gradient(rgb(250,250,250), rgb(200,200,200)) fixed;
  background: linear-gradient(rgb(250,250,250), rgb(200,200,200)) fixed;
  overflow: hidden;
}



